<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div>
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ $t('AddRule') }}</span>
                    </div>
                </div>
            </div>
        </div>
        <div class="border p-3">
            <dynamic-form ref="RuleDataList" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                <template v-slot:tgslot-ApplyOnSLA="{ data }">
                    <div class="div-scroll">
                        <div class="outer DivRuleFlowDataShowCls flowchart-fix" id="divruleflowdatashow"
                            v-if="isShowFlowChart">
                            <ul class="fc_parentchild">
                                <li class="arrowdown">
                                    <p class="shape_oval shape_ovalwhen">
                                        <span>When</span>
                                    </p>
                                    <ul class="fc_subchild fc_subchildWhen">
                                        <li class="arrowright top50 m-0">
                                            <div class="formsfieldmainunique">
                                                <div class="parenthead">
                                                    <span class="text">When do you want to execute the rule () ?</span>
                                                    <span :class="{ 'icon': true, 'd-none': !isShowBackIcon }"
                                                        @click="GotoBack" title="Back" data-event="hide"></span>
                                                </div>
                                                <div :class="{ 'childdivsunique_edit': true, 'd-none': !isShowEdit }">
                                                    <div class="childdivsuniqueheader">
                                                        <span>Routing: {{ SelectedRouting }} </span>
                                                    </div>
                                                    <div class="childdivediticon_unique textalignright d-block">
                                                        <a class="d-block">
                                                            <div class="iconedit_unique" @click="EditTarget"></div>
                                                        </a>
                                                    </div>
                                                </div>
                                                <div :class="{ 'childdivsunique_base': true, 'd-none': isShowEdit }">
                                                    <div class="childdivsunique childdivsunique_HS color757575"
                                                        v-for="(item, index) in RuleEventList" :key="index">
                                                        <input type="radio" name="radioevent" class="clsradioevent d-block"
                                                            style="display: none;"
                                                            v-on:change="OnChangeEvent(item, null, null)">
                                                        <label class="offrd"></label>
                                                        <span>{{ item.EVENT_NAME }}</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </li>
                                    </ul>
                                </li>
                                <template v-for="(item, index) in AddDeleteTarget" v-if="AddDeleteTarget.length > 0">
                                    <li class="clstargetadd arrowdown" :id="'li_' + index"
                                        :data-eventid="item.RULE_EVENT_ID">
                                        <p class="shape_polygan">
                                            <span>{{ $t('Target') }}</span>
                                            <span class="delete" title="Click here to delete Target"
                                                @click="DeleteTarget(index)"></span>
                                        </p>
                                        <ul class="fc_subchild">
                                            <li class="arrowright" :id="'litarger_' + index"
                                                v-if="item.TargetRuleCondition == ''">
                                                <p class="shape_btn">
                                                    <a class="addtarget_btn" v-on:click="AddTarget(index)">{{
                                                        $t('AddTarget')
                                                    }}</a>
                                                </p>
                                            </li>
                                            <li class="arrowright" :id="'litarger_' + index"
                                                v-if="item.TargetRuleCondition != ''">
                                                <div class="formsfieldmainunique_condition">
                                                    <div class="innercontbase">
                                                        <div class="innercont">{{ item.TargetRuleCondition }} </div>
                                                        <div class="inneredit d-block">
                                                            <a class="d-block">
                                                                <div class="iconedit_unique"
                                                                    @click="EditTargetCondition(item.TargetRuleCondition, item.TargetData, item.TargetRuleDataBaseCondition, index)">
                                                                </div>
                                                            </a>
                                                        </div>
                                                    </div>
                                                </div>
                                            </li>
                                            <li class="arrowright" :id="'litarger_' + index"
                                                v-if="item.ResultCondition == '' && item.TargetRuleCondition != ''">
                                                <p class="shape_btn">
                                                    <a class="addtarget_btn targetresult" @click="AddResult(index)">{{
                                                        $t('AddResult')
                                                    }}</a>
                                                </p>
                                            </li>
                                            <li class="arrowright" :id="'litarger_' + index"
                                                v-if="item.ResultCondition != ''">
                                                <p class="shape_oval">
                                                    <span class="results" :title="'ResultType: ' + item.ResultCondition"
                                                        style="white-space: nowrap;">{{ $t('ResultType') }}:{{
                                                            item.ResultCondition
                                                        }}</span>
                                                    <input type="hidden" class="hdnTargetsResultText" value=""><a
                                                        @click="EdiResultCondition(item.ruleResultEditDataArray, index)"
                                                        class="resulteditlink mt-3"
                                                        title="Click here to edit Target Result"></a>
                                                </p>
                                            </li>
                                        </ul>
                                    </li>
                                </template>
                                <li class="arrowdown" v-if="isShowAddMoreTarget">
                                    <p class="shape_roundbtn" title="Click here to add new Target" @click="AddMoreTarget">
                                        <span>+</span>
                                    </p>
                                </li>
                            </ul>
                        </div>
                    </div>
                </template>
            </dynamic-form>
        </div>
        <addtarget :acionId="ActionId" eventIds="" :TargetIndex="TargetIndex" :moduleName="ModuleName"
            @close="isShowTargetModal = false;" v-if="isShowTargetModal" @GetformFields="GetformFields"
            :TargetConditionData="TargetConditionData"></addtarget>
        <addResult :acionId="ActionId" :ResultIndex="ResultIndex" @GetformResultFields="GetformResultFields"
            :ruleResultEditDataArray="ruleResultEditDataArray" :eventId="EventId" :moduleGroupId="moduleGroupId"
            :moduleName="ModuleName" @close="isShowResultModal = false;" v-if="isShowResultModal"></addResult>
    </section>
</template>

<script>
import DataService from "../../services/DataService";
import addtarget from "../../components/Modal/modalAddTarget.vue"
import addResult from "../../components/Modal/modaladdResult.vue"
export default {
    components: { addtarget, addResult },
    data() {
        return {
            isLoading: false,
            ModuleList: [],
            RuleEventList: [],
            EventId: 0,
            TargetIndex: 0,
            ResultIndex: 0,
            moduleGroupId: 0,
            AddDeleteTarget: [],
            isShowFlowChart: false,
            isShowEdit: false,
            isShowBackIcon: false,
            SelectedRouting: '',
            isShowTargetModal: false,
            TargetConditionData: [],
            ResultConditionData: [],
            isShowResultModal: false,
            isShowAddMoreTarget: false,
            ModuleName: '',
            ActionId: 0,
            FormSchema: [
                {
                    layoutType: 'triple',
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('RuleName'),
                            name: "RuleName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('moduleName'),
                            name: "SelectModule",
                            value: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeDropdown
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('ActionName'),
                            name: "SelectAction",
                            value: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.OnChangeRuleAction
                            },
                            validationRules: { "required": true },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            placeholder: "Enter Description",
                            validationRules: "required",
                            config: {
                                rows: 5
                            }
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "slotField",
                            label: "",
                            name: "ApplyOnSLA",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.onCancel
                }
            ],
            GetDDLTicketCategoryByCompanyId: [],
            GetCompanyClientByCompanyId: [],
            GetCompanyUserByCompanyId: [],
            SlaProductList: [],
            GetDepartmentListbyCompanyForClient: [],
            YesorNoList: [],
            GetDDLTicketZoneByCompanyId: [],
            // GetWorkTypeList: [],
            GetSkillTypeList: [],
            GetExpierenceTypeList: [],
            GetTicketChainList: [],
        }
    },
    created: async function () {
        var vm = this;
        vm.ModuleGroupNameList();
        // await vm.GetWorkTypeListing();
        if (this.$route.params.id > 0) {
            vm.GetRuleDetailByRuleId(this.$route.params.id);
        }
    },
    methods: {
        onSubmit: function (formfields, val) {
            var vm = this;
            vm.buttons[0].class+=" disabled";
            var mainRuleTarget = [];
            var msg = '';
            if(vm.AddDeleteTarget.length==0){
                vm.ShowAlert(vm.$t('SlaWhenAlertMessage'), 'warning', true, vm.$t('Alert'));
                vm.buttons[0].class="btn btn-success";
                return false;
            }else{
            vm.AddDeleteTarget.forEach(item => {
                var ruleTargetList = [];
                var jsonruleConditionTargetObj = [];
                if (item.TargetData == "") {
                    vm.ShowAlert(vm.$t('TargetResultRequired'), 'warning', true, vm.$t('Alert'));
                    vm.buttons[0].class="btn btn-success";
                    msg = 'no'
                    return false;
                }
                item.TargetData.forEach((itm, index) => {
                    var data = '';
                    var testData = '';
                    if (itm.multiselector != null && itm.multiselector != '') {
                        itm.multiselector.forEach((items, index) => {
                            if (index == 0) {
                                data = items.value;
                            }
                            else {
                                data += ',' + items.value;
                            }
                        })
                        testData = data;
                    } else {
                        testData = itm.selectorField;
                    }
                    var ListTargetObj = {
                        TargetId: index + 1,
                        OpeningBrackets: "(",
                        FieldValue: testData,
                        FieldSecondValue: itm.TextTypeField,
                        ClosingBrackets: ")",
                        RelationWithParent: itm.condition,
                        FieldId: itm.RuleTarget,
                        OperatorId: itm.OperatorId,
                        RuleCondition: item.TargetRuleCondition,
                        RuleDatabaseCondition: item.TargetRuleDataBaseCondition,
                        RuleTargetResultCondition: item.ResultCondition,
                        IsTime: 0
                    }
                    ruleTargetList.push(ListTargetObj);
                });
                if (item.ruleTargetResultData == "") {
                    vm.ShowAlert(vm.$t('TargetResultRequired'), 'warning', true, vm.$t('Alert'));
                    vm.buttons[0].class="btn btn-success";
                    msg = 'no'
                    return false;
                }
                item.ruleTargetResultData.forEach((itms, index) => {
                    var ListResultObj = {
                        ResultChainGroup: itms.ResultChainGroup,
                        ResultChainIds: itms.ResultChainIds,
                        ResultOtherId: itms.ResultOtherId,
                        ResultType: itms.ResultType,
                        WarningMessageTypeId: itms.WarningMessageTypeId
                    }
                    jsonruleConditionTargetObj.push(ListResultObj);
                });
                var targetResult = {
                    RuleTargetList: ruleTargetList,
                    RuleTargetResult: jsonruleConditionTargetObj
                }
                mainRuleTarget.push(targetResult);
            });
        }
            if (msg == 'no') {
                vm.buttons[0].class="btn btn-success";
                return false;
            }
            var obj = {
                RuleId: this.$route.params.id == undefined ? 0 : this.$route.params.id,
                RuleName: formfields.RuleName,
                ModuleId: formfields.SelectModule,
                Description: formfields.Description,
                ActionId: formfields.SelectAction,
                RuleTarget: mainRuleTarget,
                EventId: vm.EventId,
                IsForSla: 0
            };
            var params = JSON.stringify(obj);
            DataService.SaveRuleEngine(params).then((response) => {
                if (response.data.STATUS_CODE == 0) {
                    var RuleId = response.data.RULE_ID;
                    if (vm.$route.params.id > 0) {
                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("RuleEngine")]), "success", true, vm.$t("Alert"));
                        vm.onCancel();
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("RuleEngine")]), "success", true, vm.$t("Alert"));
                    }
                    vm.onCancel();
                    vm.$parent.FetchData();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Policy")]), "failure", true, vm.$t("Alert"));
                    vm.onCancel();
                }
            });

        },
        // GetWorkTypeListing: async function () {
        //     var vm = this;
        //     var appendingUrl = `id=&&moduleName=${'TICKETING'}&type=${'FSM_WORKTYPE'}&search=&refId=`;
        //     await DataService.GetDDLDataFSM(appendingUrl).then(function (response) {
        //         if (response.data) {
        //             var json = response.data;
        //             debugger;
        //             if (json.result == "success") {
        //                 if (json != null) {
        //                     console.log(response.data.result.DATA);
        //                     if (response.data.result.DATA.length > 0) {
        //                         response.data.result.DATA.forEach((item, index) => {
        //                             vm.GetWorkTypeList.push({
        //                                 value: item.value,
        //                                 text: item.name
        //                             })
        //                         })
        //                     }

        //                 }
        //             }

        //         }
        //     });
            
        // },
        GetSkillListing: async function () {
            var vm = this;
            var appendingUrl = `id=&&moduleName=${'FSM'}&type=${'USER_SKILLS'}&search=&refId=`;
            await DataService.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.GetSkillTypeList.push({
                                value: item.value,
                                text: item.name
                            })
                        })
                    }
                }
            });
        },
        GetRuleDetailByRuleId: async function (RuleId) {
            var vm = this;
            debugger;
            var url = `actionId=${vm.ActionId}&eventids=""&moduleName=${vm.moduleName}`;
            await DataService.AddTarget(url).then(function (response) {
                debugger;
                vm.YesorNoList = response.data.YesNoList;
                let GetCompanyUser = [];
                GetCompanyUser = response.data.companyUserByCompanyId;
                if (GetCompanyUser != null) {
                    GetCompanyUser.forEach((item, index) => {
                        vm.GetCompanyUserByCompanyId.push({
                            value: item.USERID,
                            text: item.USERNAME
                        });
                    });
                }
                let GetExpierenceList = [];
                GetExpierenceList = response.data.GetExperienceList;
                if (GetExpierenceList != null) {
                    GetExpierenceList.forEach((item, index) => {
                        vm.GetExpierenceTypeList.push({
                            value: item.ID,
                            text: item.EXPERIENCE
                        })
                    });
                }
                let GetTicketChain = [];
                GetTicketChain = response.data.GetTicketChainForDropdownListByCompanyId;
                if (GetTicketChain != null) {
                    GetTicketChain.forEach((item, index) => {
                        vm.GetTicketChainList.push({
                            value: item.ticket_chain_id,
                            text: item.ticket_chain_name
                        })
                    });
                }
                let GetDDLTicketCategory = [];
                GetDDLTicketCategory = response.data.GetDDLTicketCategoryByCompanyId;
                if (GetDDLTicketCategory != null) {
                    GetDDLTicketCategory.forEach((item, index) => {
                        vm.GetDDLTicketCategoryByCompanyId.push({
                            value: item.value,
                            text: item.name
                        })
                    });
                }
                let GetCompanyClient = [];
                GetCompanyClient = response.data.GetCompanyClientByCompanyId.result;
                if (GetCompanyClient != null) {
                    GetCompanyClient.forEach((item, index) => {
                        vm.GetCompanyClientByCompanyId.push({
                            value: item.value,
                            text: item.name
                        })
                    });
                }
                let SlaProduct = [];
                SlaProduct = response.data.ProductsList.result;
                if (SlaProduct != null) {
                    SlaProduct.forEach((item, index) => {
                        vm.SlaProductList.push({
                            value: item.value,
                            text: item.name
                        })
                    });
                }
                let GetDepartmentList = [];
                GetDepartmentList = response.data.GetDepartmentListbyCompanyForClient.result;
                if (GetDepartmentList != null) {
                    GetDepartmentList.forEach((item, index) => {
                        vm.GetDepartmentListbyCompanyForClient.push({
                            value: item.departmenT_ID,
                            text: item.departmenT_NAME
                        })

                    });
                }
                let GetDDLTicketZone = [];
                GetDDLTicketZone = response.data.GetDDLTicketZoneByCompanyId;
                if (GetDDLTicketZone != null) {
                    GetDDLTicketZone.forEach((item, index) => {
                        vm.GetDDLTicketZoneByCompanyId.push({
                            value: item.ZONE_ID,
                            text: item.ZONE_NAME
                        })
                    });
                }
            });
            var url = `id=${RuleId}`;
            await DataService.GetRuleDetaildByRuleId(url).then((response) => {
                this.onChangeDropdown(null, null, response.data[0].MODULE_GROUP_ID);
                vm.ActionId = response.data[0].MODULE_ACTION_ID;
                vm.EventId = response.data[0].ACTION_EVENT_ID;
                var ApiUrl = `actionId=${response.data[0].MODULE_ACTION_ID}&eventids=${response.data[0].ACTION_EVENT_ID}`;
                DataService.GetRuleActionFields(ApiUrl).then(function (response) {
                    vm.FieldsDropDownList = response.data;
                });
            });
            await this.GetRuleEventItems(vm.ActionId, 0);
            var filterEventName = {};
            filterEventName = vm.RuleEventList.filter(x => x.RULE_EVENT_ID == vm.EventId);
            var name = filterEventName[0].EVENT_NAME;
            vm.OnChangeEvent(null, vm.EventId, name);

            var url = `fieldId=`;
            await DataService.GetOperators(url).then(function (response) {
                vm.NewOperatorListData = response.data;
            });
            var url = `id=${RuleId}`;
            await DataService.GetRuleDetaildByRuleId(url).then((response) => {
                if (response.data.length > 0) {
                    (vm.FormSchema)[0].Data[0].value = response.data[0].RULE_NAME;
                    (vm.FormSchema)[0].Data[1].value = response.data[0].MODULE_GROUP_ID;
                    (vm.FormSchema)[0].Data[2].value = response.data[0].MODULE_ACTION_ID;
                    (vm.FormSchema)[1].Data[0].value = response.data[0].RULE_DESCRIPTION;
                    vm.isShowFlowChart = true;
                    let group = [];
                    let test = {};
                    vm.EventId = response.data[0].ACTION_EVENT_ID;
                    response.data.forEach(item => {
                        var RuleId = item.RULE_ID;
                        var RuleName = item.RULE_NAME;
                        var ModuleId = item.MODULE_GROUP_ID;
                        vm.ActionId = item.MODULE_ACTION_ID;
                        var EventId = item.ACTION_EVENT_ID;
                        var Description = item.RULE_DESCRIPTION;
                        const targetId = item.TARGET_ID;
                        if (!group[targetId]) {
                            group[targetId] = {
                                RuleTargetResult: [],
                                RuleTargetList: [],
                            };
                        }
                        const ruleResultId = item.RULE_RESULT_ID;
                        if (!group[targetId].RuleTargetResult.some(result => result.RuleResultId === ruleResultId)) {
                            group[targetId].RuleTargetResult.push({
                                RuleResultId: ruleResultId,
                                WarningMessageTypeId: item.MESSAGE_TYPE_ID,
                                ResultTemplateId: item.TEMPLATE_ID,
                                IsSendToDefaultCA: item.IS_DEFAULT_CA ? 1 : 0,
                                WarningMessage: item.RESULT_MESSAGE,
                                RuleTargetResultType: item.TARGET_RESULT_TYPE,
                                RuleTargetResultCode: item.RESULT_CODE,
                                ResultChainIds: item.RESULT_CHAIN_ID,
                                ResultChain: item.TARGET_RESULT_CHAIN,
                                ResultType: item.RESULT_TYPE,
                                ResultChainGroup: item.RESULT_CHAIN_GROUP_ID,
                                ResultOtherId: item.RESULT_ID_OTHER,
                            });
                        }
                        const targetConditionId = item.TARGET_CONDITION_ID;
                        if (!group[targetId].RuleTargetList.some(list => list.TargetConditionId === targetConditionId)) {
                            group[targetId].RuleTargetList.push({
                                TargetConditionId: targetConditionId,
                                FieldId: item.FIELD_ID,
                                OperatorId: item.OPERATOR_ID,
                                FieldValue: item.FIELD_VALUE,
                                FieldSecondValue: item.FIELD_SECOND_VALUE,
                                RelationWithParent: item.RELATION_WITH_PARENT,
                                OpeningBrackets: item.OPENING_BRACKETS,
                                OperatorExpression: item.OPERATOR_EXPRESSION,
                                ClosingBrackets: item.CLOSING_BRACKETS,
                                DataType: item.DATA_TYPE,
                                RuleCondition: item.DISPLAY_CONDITION,
                                RuleDatabaseCondition: item.DATABASE_CONDITION,
                                RuleTargetResultCondition: item.TARGET_RESULT_CONDITION,
                                IsTime: item.IS_TIME,
                            });
                        }

                    });
                    var TargetData = [];
                    var TargetResultEditData = [];
                    var TargetResultData = [];
                    group.forEach((item, index) => {
                        var ListTarget = [];
                        var ListResult = [];
                        var ListResultEdit = [];
                        vm.AddDeleteTarget.push({ RULE_EVENT_ID: '', ResultCondition: '', TargetData: [], TargetRuleCondition: '', TargetRuleDataBaseCondition: '', ruleResultEditDataArray: [], ruleTargetResultData: [] });
                        item.RuleTargetList.forEach((item, indx) => {
                            var selectedValue = '';
                            var selectType = '';
                            var TextFieldValue = '';
                            var SelectFieldValue = '';
                            var MultiSelectFieldValue = '';
                            var DDlType = '';
                            var OperatorDataList = '';
                            var filterRuleName = {};
                            var NewOprationArray = [];
                            filterRuleName = vm.FieldsDropDownList.filter(x => x.COLUMN_ID == item.FieldId);
                            if (filterRuleName[0].DISPLAY_NAME == 'category' || filterRuleName[0].DISPLAY_NAME == 'client' || filterRuleName[0].DISPLAY_NAME == 'Created By' || filterRuleName[0].DISPLAY_NAME == 'product'
                                || filterRuleName[0].DISPLAY_NAME == 'department' || filterRuleName[0].DISPLAY_NAME == 'Is Field Service' || filterRuleName[0].DISPLAY_NAME == 'Ticket Area') {
                                //|| filterRuleName[0].DISPLAY_NAME == 'Type Of Work'
                                vm.NewOperatorListData.forEach(item => {
                                    if (item.OPERATOR_EXPRESSION == '=' && item.OPERATION_DATA_TYPE == 'bigint') {
                                        NewOprationArray.push({ value: item.OPERATOR_ID, name: item.OPERATOR_DISPLAY_NAME, Expression: item.OPERATOR_EXPRESSION });
                                    }
                                    if ((item.OPERATOR_EXPRESSION == 'IN' || item.OPERATOR_EXPRESSION == 'NOT IN') && item.OPERATION_DATA_TYPE == 'nvarchar') {
                                        NewOprationArray.push({ value: item.OPERATOR_ID, name: item.OPERATOR_DISPLAY_NAME, Expression: item.OPERATOR_EXPRESSION });
                                    }
                                })
                                OperatorDataList = NewOprationArray;
                            }
                            else {
                                vm.NewOperatorListData.forEach(item => {
                                    if (item.OPERATION_DATA_TYPE == 'nvarchar') {
                                        NewOprationArray.push({ value: item.OPERATOR_ID, name: item.OPERATOR_DISPLAY_NAME, Expression: item.OPERATOR_EXPRESSION });
                                    }
                                })
                                OperatorDataList = NewOprationArray;
                            }
                            if (filterRuleName[0].DISPLAY_NAME == 'Is Field Service') {
                                selectedValue = 'IsFieldService';
                            }
                            if (item.OperatorExpression == '=' && item.DataType == 'bigint') {
                                selectType = 'select';
                            }
                            else if ((item.OperatorExpression == 'IN' || item.OperatorExpression == 'NOT IN') && item.DataType == 'bigint') {
                                selectType = 'multiselect';
                            }
                            if (item.DataType == 'nvarchar' && selectedValue != 'IsFieldService') {
                                TextFieldValue = item.FieldValue == null ? item.FieldSecondValue : item.FieldValue;
                            }
                            if ((item.DataType == 'bigint' && selectType == 'select') || selectedValue == 'IsFieldService') {
                                SelectFieldValue = item.FieldValue;
                                var filterFieldServiceName = {};
                                if (selectedValue == 'IsFieldService') {
                                    var field_value = item.FieldValue == "no" ? "No" : "Yes";
                                    filterFieldServiceName = vm.YesorNoList.filter(x => x.text == field_value);
                                    SelectFieldValue = filterFieldServiceName[0].value;
                                }
                            }
                            if (item.DataType == 'bigint' && selectType == 'multiselect') {
                                MultiSelectFieldValue = item.FieldValue;
                            }
                            if (item.DataType == 'bigint' && selectType == 'multiselect') {
                                MultiSelectFieldValue = item.FieldValue;
                            }
                            if (filterRuleName[0].DISPLAY_NAME == 'category') {
                                DDlType = vm.GetDDLTicketCategoryByCompanyId;
                            } else if (filterRuleName[0].DISPLAY_NAME == 'client') {
                                DDlType = vm.GetCompanyClientByCompanyId;
                            } else if (filterRuleName[0].DISPLAY_NAME == 'Created By') {
                                DDlType = vm.GetCompanyUserByCompanyId;
                            } else if (filterRuleName[0].DISPLAY_NAME == 'product') {
                                DDlType = vm.SlaProductList;
                            } else if (filterRuleName[0].DISPLAY_NAME == 'department') {
                                DDlType = vm.GetDepartmentListbyCompanyForClient;
                            } else if (filterRuleName[0].DISPLAY_NAME == 'Is Field Service') {
                                DDlType = vm.YesorNoList;
                                selectedValue = 'IsFieldService';
                            } else if (filterRuleName[0].DISPLAY_NAME == 'Ticket Area') {
                                DDlType = vm.GetDDLTicketZoneByCompanyId;
                            } 
                            // else if (filterRuleName[0].DISPLAY_NAME == 'Type Of Work') {
                            //     DDlType = vm.GetWorkTypeList;
                            // }
                            else {
                                DDlType = '';
                            }
                            var data = '';
                            var dataValue = '';
                            var testDataMultiselect = '';
                            var testSelectFieldValue = '';
                            var MultiselectFieldValue = [];
                            var MultiSelect = MultiSelectFieldValue.split(',');
                            if (MultiSelect.length > 0 && MultiSelect[0] != '') {
                                if(DDlType != ""){
                                DDlType.forEach((itm, index) => {
                                    MultiSelect.forEach((items, index) => {
                                        if (items == itm.value) {
                                            MultiselectFieldValue.push(itm);
                                        }
                                    })
                                })
                            }
                                testDataMultiselect = MultiselectFieldValue;
                            } else if (SelectFieldValue != "" && MultiSelect.length == 1) {
                                if(DDlType != ""){
                                DDlType.forEach((itm, index) => {
                                    data = itm.text;
                                    dataValue = itm.value;
                                })
                            }
                                var testData = data;
                                testSelectFieldValue = dataValue;
                            }
                            var ListTargetObj = {
                                RuleTarget: item.FieldId,
                                OperatorId: item.OperatorId,
                                multiselector: testDataMultiselect,
                                selectorField: SelectFieldValue,
                                TextTypeField: TextFieldValue,
                                DataType: item.DataType,
                                DDlType: DDlType,
                                selectType: selectType,
                                condition: item.RelationWithParent,
                                selectedValue: selectedValue,
                                OperatorDataList: OperatorDataList,
                                ResultTargetCondition: item.RuleTargetResultCondition,
                                TargetDatabaseCondition: item.RuleDatabaseCondition,
                                TargetRuleCondition: item.RuleCondition
                            }
                            ListTarget.push(ListTargetObj);
                        });
                        TargetData.push(ListTarget);
                        TargetData.forEach((item, index1) => {
                            vm.AddDeleteTarget[index1].TargetData = item;
                            vm.AddDeleteTarget[index1].RULE_EVENT_ID = vm.EventId;
                            vm.AddDeleteTarget[index1].ResultCondition = item[0].ResultTargetCondition;
                            vm.AddDeleteTarget[index1].TargetRuleDataBaseCondition = item[0].TargetDatabaseCondition;
                            vm.AddDeleteTarget[index1].TargetRuleCondition = item[0].TargetRuleCondition;
                        });
                        item.RuleTargetResult.forEach((item, indx) => {
                            var DDlTypes = '';
                            if (item.ResultType == "department") {
                                DDlTypes = vm.GetDepartmentListbyCompanyForClient;
                            }
                            else if (item.ResultType == "skill") {
                                DDlTypes = vm.GetSkillTypeList;
                            }
                            else if (item.ResultType == "experience") {
                                DDlTypes = vm.GetExpierenceTypeList;
                            }
                            else if (item.ResultType == "serviceboard") {
                                DDlTypes = vm.GetTicketChainList;
                            }
                            else if (item.ResultType == "User") {
                                DDlTypes = vm.GetCompanyUserByCompanyId;
                            }

                            var ListResultEditObj = {
                                DDlType: DDlTypes,
                                ResultChainGroup: item.ResultChainGroup,
                                ResultOtherId: item.ResultOtherId,
                                TargetSelectValue: item.ResultType,
                                WarningMessageTypeId: item.WarningMessageTypeId,
                                WarningMessageTypeName: item.RuleTargetResultType,
                                selectedValue: item.ResultChainIds,
                            }
                            ListResultEdit.push(ListResultEditObj);

                            var ListResultObj = {
                                ResultChainGroup: item.ResultChainGroup,
                                ResultOtherId: item.ResultOtherId,
                                ResultType: item.ResultType,
                                WarningMessageTypeId: item.WarningMessageTypeId,
                                ResultChainIds: item.ResultChainIds,
                            }
                            ListResult.push(ListResultObj);
                        });
                        TargetResultEditData.push(ListResultEdit);
                        TargetResultData.push(ListResult);
                        TargetResultEditData.forEach((item, index1) => {
                            vm.AddDeleteTarget[index1].ruleResultEditDataArray = item;
                        });
                        TargetResultData.forEach((item, index1) => {
                            vm.AddDeleteTarget[index1].ruleTargetResultData = item;
                        });
                        console.log("cfdsd", vm.AddDeleteTarget)
                    });
                }
            });
        },
        onCancel: function () {
            this.$router.push({ name: 'RuleEngine' });
        },
        onChangeDropdown: async function (e, field, id) {
            var vm = this;
            if (e == null) {
                vm.moduleGroupId = id;
                vm.ModuleName = vm.ModuleList.filter(x => x.value == id)[0].name;
                await vm.GetRuleActionItems(id);
            } else {
                vm.moduleGroupId = e.target.value;
                vm.ModuleName = vm.ModuleList.filter(x => x.value == e.target.value)[0].name;
                await vm.GetRuleActionItems(e.target.value);
            }
        },
        ModuleGroupNameList: async function () {
            var vm = this;
            vm.isLoading=true;
            await DataService.ModuleGroupNameList().then(function (response) {
                vm.ModuleList = [];
                var moduleList = response.data;
                if (moduleList.length > 0) {
                    moduleList.forEach(item => {
                        let obj = {
                            name: vm.$t(item.MODULE_GROUP_NAME),
                            value: item.MODULE_GROUP_ID
                        }
                        // if (obj.name != "Field Service Management" && obj.name != "Approval" && obj.name !="Product Management") {
                        //     vm.ModuleList.push(obj);
                        // }
                        if (obj.name == "Ticketing") {
                            vm.ModuleList.push(obj);
                        }
                    })
                }
                (vm.FormSchema)[0].Data[1].config.options = vm.ModuleList.sort((a, b) => a.name.toLowerCase() > b.name.toLowerCase() ? 1 : -1);
                vm.$refs.RuleDataList.UpdateKeyValue();
                vm.isLoading=false;
            })
        },
        GetformFields: async function (TargetData, TargetCondition, TargetDataBaseCondition, index) {
            var vm = this;
            vm.AddDeleteTarget[index].TargetRuleCondition = TargetCondition;
            vm.AddDeleteTarget[index].TargetRuleDataBaseCondition = TargetDataBaseCondition;
            vm.AddDeleteTarget[index].TargetData = TargetData;
        },
        GetformResultFields: async function (ruleTargetResultArray, msg, ruleResultEditDataArray, index) {
            var vm = this;
            vm.isShowAddMoreTarget = true;
            debugger;
            vm.AddDeleteTarget[index].ruleResultEditDataArray = ruleResultEditDataArray;
            vm.AddDeleteTarget[index].ruleTargetResultData = ruleTargetResultArray;
            vm.AddDeleteTarget[index].ResultCondition = msg;
        },
        GetRuleActionItems: async function (moduleGroupId) {
            var vm = this;
            var url = `groupId=${moduleGroupId}`;
            await DataService.GetRuleActionItems(url).then(function (response) {
                var ActionItem = [];
                var actionList = response.data;
                if (actionList.length > 0) {
                    actionList.forEach(item => {
                        let obj = {
                            name: vm.$t(item.DISPLAY_NAME),
                            value: item.ACTION_ID,
                            firstAttribute: item.MODULE_ID,
                            secondAttribute: item.MODULE_NAME
                        }
                        ActionItem.push(obj);
                    })
                    ActionItem = ActionItem.sort((a, b) => a.name.toLowerCase() > b.name.toLowerCase() ? 1 : -1);
                }
                (vm.FormSchema)[0].Data[2].config.options = ActionItem;
               vm.$refs.RuleDataList.UpdateKeyValue();
            })
        },
        OnChangeRuleAction: async function (event) {
            var vm = this;
            vm.ActionId = event.target.value;
            var url = `actionId=${event.target.value}`;
            await DataService.RuleEventItems(url).then(function (response) {
                if (response.data.length > 0) {
                    vm.RuleEventList = response.data;
                    vm.isShowFlowChart = true;
                }
                else {
                    vm.RuleEventList = [];
                    vm.isShowFlowChart = false;
                }
            }, function (error) {
                vm.isShowFlowChart = false;
            })
        },
        GetRuleEventItems: async function (actionId, eventId) {
            var vm = this;
            var url = `actionId=${actionId}`;
            await DataService.RuleEventItems(url).then(function (response) {
                if (eventId > 0) {
                    vm.RuleEventList = response.data;
                }
                else {
                    vm.RuleEventList = response.data;
                }
            });
        },
        OnChangeEvent: async function (item, EventId, EventName) {
            var vm = this;
            if (item == null) {
                vm.AddDeleteTarget = [];
                vm.SelectedRouting = EventName;
                vm.isShowEdit = true;
                vm.isShowBackIcon = false;
                vm.EventId = EventId;
            } else {
                vm.AddDeleteTarget = [];
                vm.SelectedRouting = item.EVENT_NAME;
                vm.isShowEdit = true;
                vm.isShowBackIcon = false;
                vm.EventId = item.RULE_EVENT_ID;
                var obj = {
                    RULE_EVENT_ID: item.RULE_EVENT_ID,
                    TargetRuleCondition: '',
                    TargetRuleDataBaseCondition: '',
                    TargetData: [],
                    ResultCondition: '',
                    ruleTargetResultData: [],
                    ruleResultEditDataArray: []
                }
                vm.AddDeleteTarget.push(obj);
            }
        },
        GotoBack: async function () {
            var vm = this;
            vm.isShowEdit = true;
            vm.isShowBackIcon = false;
        },
        EditTarget: async function () {
            var vm = this;
            vm.isShowEdit = false;
            vm.isShowBackIcon = true;
            vm.isShowFlowChart = true;
        },
        AddTarget: async function (id) {
            var vm = this;
            vm.TargetConditionData = "";
            vm.TargetRuleDataBaseCondition = "";
            vm.TargetIndex = id;
            vm.isShowTargetModal = true;
        },
        EditTargetCondition: async function (TargetRuleCondition, TargetData, TargetDataBaseCondition, id) {
            var vm = this;
            vm.TargetRuleDataBaseCondition = TargetDataBaseCondition;
            vm.TargetConditionData = TargetData;
            vm.TargetIndex = id;
            vm.isShowTargetModal = true;
        },
        EdiResultCondition: async function (ruleResultEditDataArray, id) {
            var vm = this;
            vm.ruleResultEditDataArray = ruleResultEditDataArray;
            vm.ResultIndex = id;
            vm.isShowResultModal = true;
        },
        AddResult: async function (id) {
            var vm = this;
            vm.ruleResultEditDataArray = [];
            vm.ResultIndex = id;
            vm.isShowResultModal = true;
        },
        AddMoreTarget() {
            var vm = this;
            var obj = {
                RULE_EVENT_ID: 0,
                TargetRuleCondition: '',
                TargetRuleDataBaseCondition: '',
                TargetData: [],
                ResultCondition: '',
                ruleTargetResultData: [],
                ruleResultEditDataArray: []
            }
            vm.AddDeleteTarget.push(obj);
            vm.isShowAddMoreTarget = false;
        },
        DeleteTarget: async function (index) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDeleteTarget"), true, vm.$t("Delete"), function () {
                vm.AddDeleteTarget.splice(index, 1);
                if (index > 0) {
                    vm.isShowAddMoreTarget = true;
                } else {
                    vm.isShowAddMoreTarget = false;
                }
            });
        }
    }
}
</script>
