<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('Result') }}</h4>
                        <button type="button" class="close" @click="$emit('close')">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                        <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG" role="button"
                                aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('UgAddResultTarget')"></div>
                                </div>
                            </div>
                        </span>
                    </div>
                    <div class="modal-body">
                        <Form ref="ManageRuleEngineResultTarget">
                            <div class="DivRuleFlowDataShowCls flowfsm" id="divruleflowdatafsmshow"
                                v-for="(item, index) in Mainleveldiv" :key="index">
                                <div class="fc_parentchild">
                                    <div class="row">
                                        <div class="col-md-12 arrowdown" v-if="index == 0">
                                            <div class="py-3 px-3 bg-color-yellow">
                                                <Field name="ddlwarningMessageTypeId" v-slot="{ errors }"
                                                    :rules="{ 'required': true }">
                                                    <select id="ddlwarningMessageTypeId" v-model="WarningMessageTypeId"
                                                        :class="{ 'form-control customddl ddlwarningMessageTypeId inline-block text-truncate': true, 'is-invalid': errors[0] }"
                                                        v-on:change="OnChangeEvent(items, $event, index)"
                                                        name="ddlwarningMessageTypeId">
                                                        <option value="">{{ $t("Select") }}</option>
                                                        <option v-for="item in GetEventResultTypeDropdown"
                                                            :value="item.value">
                                                            {{ $t(item.text) }}
                                                        </option>
                                                    </select>
                                                </Field>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="clstargetadd div-li mt-4 float-left w-100" :class="item.arrowClass"
                                        id="li_0">
                                        <div class="formsfieldmainunique_condition">
                                            <div class="innercontbase p-3">
                                                <div class="row" v-if="ShowSendResponsediv">
                                                    <div class="col-md-6" v-if="IsShowdisableField == true">
                                                        <div class="w-100 float-left" v-if="CheckExtraFeatures == true">
                                                            <Field :name="'TargetSelectValue' + index" v-slot="{ errors }"
                                                                :rules="{ 'required': true }">
                                                                <select
                                                                    :class="{ 'form-control ddlresultCriteria': true, 'is-invalid': errors[0] }"
                                                                    v-model="item.TargetSelectValue"
                                                                    v-on:change="OnChangeField(items, $event)">
                                                                    <option value="">{{ $t('Select') }}</option>
                                                                    <option value="offhours">{{ $t('BasedOnOffHours') }}
                                                                    </option>
                                                                    <option value="department">{{ $t('Department') }}
                                                                    </option>
                                                                    <option value="experience">{{ $t('Experience') }}
                                                                    </option>
                                                                    <option value="serviceboard">{{ $t('ServiceBoard') }}
                                                                    </option>
                                                                    <option value="skill">{{ $t('Skill') }} </option>
                                                                    <option value="User">{{ $t('Users/Technician') }}
                                                                    </option>
                                                                </select>
                                                            </Field>
                                                        </div>
                                                        <div class="w-100 float-left" v-if="CheckExtraFeatures == false">
                                                            <Field :name="'TargetSelectValue' + index" v-slot="{ errors }"
                                                                :rules="{ 'required': true }">
                                                                <select
                                                                    :class="{ 'form-control ddlresultCriteria': true, 'is-invalid': errors[0] }"
                                                                    v-model="item.TargetSelectValue"
                                                                    v-on:change="OnChangeField(items, $event, index)">
                                                                    <option value="">{{ $t('Select') }}</option>
                                                                    <option value="offhours">{{ $t('BasedOnOffHours') }}
                                                                    </option>
                                                                    <option value="department">{{ $t('Department') }}
                                                                    </option>
                                                                    <option value="experience">{{ $t('Experience') }}
                                                                    </option>
                                                                    <option value="locatorarea">{{ $t('LocatorArea') }}
                                                                    </option>
                                                                    <option value="serviceboard">{{ $t('ServiceBoard') }}
                                                                    </option>
                                                                    <option value="skill">{{ $t('Skill') }} </option>
                                                                    <option value="User">{{ $t('Users/Technician') }}
                                                                    </option>
                                                                </select>
                                                            </Field>
                                                        </div>
                                                    </div>

                                                    <div class="col-md-6" v-if="IsShowdisableField == false">
                                                        <div class="w-100 float-left ">
                                                            <select class="form-control ddlresultCriteria disabled"
                                                                disabled="disabled">
                                                                <option value="">{{ $t('Select') }}</option>
                                                                <option value="offhours">{{ $t('BasedOnOffHours') }}
                                                                </option>
                                                                <option value="department">{{ $t('Department') }}</option>
                                                                <option value="experience">{{ $t('Experience') }}</option>
                                                                <option value="locatorarea">{{ $t('LocatorArea') }}</option>
                                                                <option value="serviceboard">{{ $t('ServiceBoard') }}
                                                                </option>
                                                                <option value="skill">{{ $t('Skill') }} </option>
                                                                <option value="User" selected>{{ $t('Users/Technician') }}
                                                                </option>
                                                            </select>
                                                        </div>
                                                    </div>

                                                    <div class="col-md-6" v-if="SelectAssignTo == true">
                                                        <div class="input-group">
                                                            <Field :name="'selectedValue' + index" v-slot="{ errors }"
                                                                :rules="{ 'required': true }">
                                                                <select
                                                                    :class="{ 'form-control ddlTargetResult': true, 'is-invalid': errors[0] }"
                                                                    data-id="1" id="ddl_1" v-if="IsShowDdlTarget"
                                                                    v-model="item.selectedValue">
                                                                    <option value>{{ $t("Select") }}</option>
                                                                    <option v-for="item in item.DDlType"
                                                                        :value="item.value">{{
                                                                            $t(item.text) }}
                                                                    </option>
                                                                </select>
                                                            </Field>
                                                            <span class="input-group-text border-0 bg-transparent p-0 pl-2"
                                                                v-if="IsShowAddBtn && index == 0">
                                                                <a class="d-block ancaddresultrow" style="cursor: pointer;"
                                                                    data-placement="left" data-index="0" href="javascript:;"
                                                                    title="Add" @click="AddMoreData()">
                                                                    <i class="fa f30 fa-plus-circle"></i>
                                                                </a>
                                                            </span>
                                                            <span class="input-group-text border-0 bg-transparent p-0 pl-2"
                                                                v-if="index != 0">
                                                                <a id="idd_2"
                                                                    class="btn btn-danger text-white deleteresultrow"
                                                                    @click="DeleteUser(index)" title="Delete"><i
                                                                        class="fa f30 fa-minus-circle"></i></a>
                                                            </span>
                                                        </div>
                                                    </div>
                                                    <div class="col-md-6" v-if="SelectAssignTo == false">
                                                        <div class="hs_tag_general tagsModals">
                                                          
                                                            <Field :name="'selectedValue'" v-slot="{ errors }"
                                                                :rules="{ 'required': true }">
                                                                <multiselect v-model="SelectedUser" id="ddlfirstfield"
                                                                    :class="{ 'form-group tt_ddr_01 col tt_bod-right ddlfirstfield': true, 'is-invalid': errors[0] }"
                                                                    :limit="1" name="ddlfirstfield" label="text"
                                                                    track-by="value"
                                                                    v-bind:options="GetCompanyUserByCompanyId"
                                                                    v-bind:multiple="true" v-bind:taggable="true">
                                                                </multiselect>
                                                                <span v-if="errors[0]" :name="'selectedValue'"
                                                                    class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                            </Field>
                                                        </div>


                                                    </div>
                                                    <!-- class="form-group tt_ddr_01 col border-0 tt_bod-right" -->
                                                </div>
                                                <div class="row" v-if="SelectAssignTo == false && ShowSendResponsediv">
                                                    <div class="col-md-6">
                                                        <div class="w-100 float-left">
                                                            <select class="form-control ddlTargetResult" data-id="1"
                                                                v-model="EmailTemplate" id="ddl_1" v-if="IsShowDdlTarget">
                                                                <option value>{{ $t("SelectEmailTemplate") }}</option>
                                                                <option v-for="item in GetEmailTemplateDDlList"
                                                                    :value="item.value">{{
                                                                        $t(item.text) }}
                                                                </option>
                                                            </select>
                                                        </div>
                                                    </div>
                                                    <div class="col-md-6" v-if="SelectCloseTicketTo">
                                                        <div class="w-100 float-left">
                                                            <select class="form-control ddlTargetResult" data-id="1"
                                                                v-model="NotificationList" id="ddl_1"
                                                                v-if="IsShowDdlTarget">
                                                                <option value>{{ $t("SelectManageNotifications") }}</option>
                                                                <option v-for="item in GetNotificationDDlList"
                                                                    :value="item.value">{{
                                                                        $t(item.text) }}
                                                                </option>
                                                            </select>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div style="clear:both"></div>
                                        </div>
                                        <div style="clear:both"></div>
                                    </div>
                                </div>
                            </div>
                            <div class="row">
                                <div class="col-md-12 text-right mt-3">
                                    <a id="btnSaveTargetResult" class="btn btn-success mr-2 mt-3" title="Save"
                                        @click="btnSaveTargetResult"><i class="fa fa-save pr-2"></i><span>{{ $t("Save")
                                        }}</span></a>
                                    <a id="ancTargetResultDialog" v-on:click="Close()" class="btn btn-danger mt-3"
                                        title="Cancel"><i class="fa fa-close pr-2"></i><span>{{ $t("Cancel") }}</span></a>
                                </div>
                            </div>
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from "../../services/DataService";
export default {
    props: {
        acionId: {
            type: String,
            required: true
        },
        eventId: {
            type: Number,
            required: true
        },
        moduleGroupId: {
            type: String,
            required: true
        },
        moduleName: {
            type: String,
            required: true
        },
        ruleResultEditDataArray: {
            type: Array,
            required: true
        },
        ResultIndex: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            CheckExtraFeatures: false,
            GetEventResultTypeDropdown: [],
            Mainleveldiv: [{ selectedValue: '', DDlType: '', TargetSelectValue: '', arrowClass: '' }],
            GetDepartmentListbyCompanyForClient: [],
            GetSkillTypeList: [],
            GetExpierenceTypeList: [],
            GetTicketChainList: [],
            GetCompanyUserByCompanyId: [],
            GetEmailTemplateDDlList: [],
            GetNotificationDDlList: [],
            SelectedUser: [],
            IsShowDdlTarget: true,
            IsShowAddBtn: true,
            SelectAssignTo: true,
            SelectCloseTicketTo: false,
            ShowSendResponsediv: true,
            IsShowdisableField: true,
            WarningMessageTypeId: '',
            EmailTemplate: '',
            NotificationList: '',
            selectedMessegeName: '',
            IsSelectedValue: ''
        }
    },
    created: async function () {
        var vm = this;
        await vm.GetEventResultType(vm.eventId);
        await vm.CheckFeaturesForCompany();
        await vm.FetchData();
        this.GetSkillListing();
        this.GetResolveTicketListing();
        await vm.GetEditResultCondition();
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            var url = `actionId=${vm.moduleGroupId}&eventids=${vm.eventids}&moduleName=${vm.moduleName}`;
            await DataService.AddTarget(url).then(function (response) {
                let GetTicketChain = [];
                GetTicketChain = response.data.GetTicketChainForDropdownListByCompanyId;
                GetTicketChain.forEach((item, index) => {
                    vm.GetTicketChainList.push({
                        value: item.ticket_chain_id,
                        text: item.ticket_chain_name
                    })
                });
                let GetExpierenceList = [];
                GetExpierenceList = response.data.GetExperienceList;
                GetExpierenceList.forEach((item, index) => {
                    vm.GetExpierenceTypeList.push({
                        value: item.ID,
                        text: item.EXPERIENCE
                    })
                });
                let GetEmailTemplate = [];
                GetEmailTemplate = response.data.GetEmailTemplateList;
                GetEmailTemplate.forEach((item, index) => {
                    vm.GetEmailTemplateDDlList.push({
                        value: item.TEMPLATE_ID,
                        text: item.TEMPLATE_NAME
                    })
                });
                let GetNotification = [];
                GetNotification = response.data.GetNotificationType;
                GetNotification.forEach((item, index) => {
                    vm.GetNotificationDDlList.push({
                        value: item.ID,
                        text: item.TYPE_NAME
                    })
                });
                let GetDepartmentList = [];
                GetDepartmentList = response.data.GetDepartmentListbyCompanyForClient.result;
                if (GetDepartmentList != null) {
                    GetDepartmentList.forEach((item, index) => {
                        vm.GetDepartmentListbyCompanyForClient.push({
                            value: item.departmenT_ID,
                            text: item.departmenT_NAME
                        })

                    });
                }
            })
        },
        CheckFeaturesForCompany: async function () {
            var vm = this;
            var appendingUrl = `featureCode=${'ACCOUNTSETUP'}`;
            await DataService.CheckExtraFeatures(appendingUrl).then(function (response) {
                if (response.data == true) {
                    vm.CheckExtraFeatures = true;
                } else {
                    vm.CheckExtraFeatures = false;
                }
            });
        },
        GetSkillListing: async function () {
            var vm = this;
            var appendingUrl = `id=&&moduleName=${'Ticketing'}&type=${'USER_SKILLS'}&search=&refId=`;
            await DataService.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json.result == "success") {
                        if (json != null) {
                            if (response.data.result.DATA.length > 0) {
                                response.data.result.DATA.forEach((item, index) => {
                                    vm.GetSkillTypeList.push({
                                        value: item.value,
                                        text: item.name
                                    })
                                })
                            }
                        }
                    }
                }
            });
        },
        GetResolveTicketListing: async function () {
            var vm = this;
            var params = `departmentId=&skillId=`
            await DataService.GetResolveTicketUserList(params).then(function (response) {
                //debugger;
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach((item, index) => {
                            vm.GetCompanyUserByCompanyId.push({
                                value: item.userId,
                                text: item.userName
                            })
                        })
                    }
                }


            });
        },
        GetEventResultType: async function (EventId) {
            var vm = this;
            var appendingUrl = `eventId=${EventId}`;
            await DataService.GetEventResultType(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach((item, index) => {
                            vm.GetEventResultTypeDropdown.push({
                                value: item.RESULT_ID,
                                text: item.DISPLAY_NAME
                            })
                        })
                    }
                }
            });
        },
        AddMoreData() {
            var vm = this;
            vm.Mainleveldiv[vm.Mainleveldiv.length - 1].arrowClass = 'arrowdown AndClass';
            vm.Mainleveldiv.push({
                DDlType: '',
                TargetSelectValue: '',
                selectedValue: '',
            })

        },
        DeleteUser: function (id) {
            var vm = this;
            vm.Mainleveldiv.splice(id, 1);
            vm.Mainleveldiv[vm.Mainleveldiv.length - 1].arrowClass = '';
        },
        OnChangeField: async function (item, event, index) {
            var vm = this;
            var selectedValue = event.target.value;
            var ServiceSameFieldMessage = true;
            var obj = [];
            vm.Mainleveldiv.forEach((itm, index) => {
                if (itm.TargetSelectValue == selectedValue) {
                    obj.push({ selectValue: itm.TargetSelectValue })
                    if (obj.length > 1) {
                        ServiceSameFieldMessage = false;
                    }
                } else {
                    ServiceSameFieldMessage = true;
                }
//debugger;
                if (ServiceSameFieldMessage == true) {
                vm.IsShowDdlTarget = true;
                if (selectedValue == "department") {
                    vm.Mainleveldiv[index].DDlType = vm.GetDepartmentListbyCompanyForClient;
                    vm.IsShowAddBtn = true;
                }
                else if (selectedValue == "skill") {
                    vm.Mainleveldiv[index].DDlType = vm.GetSkillTypeList;
                    vm.IsShowAddBtn = true;
                }
                else if (selectedValue == "experience") {
                    vm.Mainleveldiv[index].DDlType = vm.GetExpierenceTypeList;
                    vm.IsShowAddBtn = true;
                }
                else if (selectedValue == "serviceboard") {
                    vm.Mainleveldiv[index].DDlType = vm.GetTicketChainList;
                    vm.IsShowAddBtn = true;
                }
                else if (selectedValue == "User") {
                    if (index > 0) {
                        vm.Mainleveldiv[index].TargetSelectValue = '';
                        vm.ShowAlert(vm.$t('cannotcombineresultconditions'), 'warning', true, vm.$t('Alert'));
                    } else {
                        vm.Mainleveldiv[index].DDlType = vm.GetCompanyUserByCompanyId;
                        vm.IsShowAddBtn = true;
                    }
                }
                else if (selectedValue == "locatorarea" || selectedValue == "offhours") {
                    vm.IsSelectedValue = selectedValue;
                    if (index > 0) {
                        vm.Mainleveldiv[index].TargetSelectValue = '';
                        vm.Mainleveldiv[index].DDlType = '';
                        vm.ShowAlert(vm.$t('cannotcombineresultconditions'), 'warning', true, vm.$t('Alert'));
                    } else {
                        vm.Mainleveldiv[index].DDlType = '';
                        vm.IsShowDdlTarget = false;
                        vm.IsShowAddBtn = false;
                    }
                }
            }
            else {
                vm.Mainleveldiv[index].TargetSelectValue = '';
                vm.ShowAlert(vm.$t('ServiceSameFieldMessage'), 'failure', true, vm.$t('Alert'));
            }
            });
           
        },
        OnChangeEvent: async function (item, event) {
            var vm = this;
            vm.WarningMessageTypeId = event.target.value;
            var selectedValue = event.target.selectedOptions[0].innerHTML.trim();
            vm.selectedMessegeName = selectedValue;
            if (selectedValue == "Send Email Notification") {
                vm.ShowSendResponsediv = true;
                vm.SelectAssignTo = false;
                vm.SelectCloseTicketTo = true;
                vm.IsShowdisableField = false;
                vm.Mainleveldiv = [{ selectedValue: '', DDlType: vm.GetCompanyUserByCompanyId, TargetSelectValue: 'User' }];
            }
            else if (selectedValue == "Close Ticket") {
                vm.ShowSendResponsediv = true;
                vm.SelectAssignTo = false;
                vm.SelectCloseTicketTo = false;
                vm.IsShowdisableField = false;
                vm.Mainleveldiv = [{ selectedValue: '', DDlType: vm.GetCompanyUserByCompanyId, TargetSelectValue: 'User' }];
            }
            else if (selectedValue == "Send Response To Call center") {
                vm.ShowSendResponsediv = false;
                vm.Mainleveldiv = [{ selectedValue: '', DDlType: '', TargetSelectValue: '' }];
            }
            else if (selectedValue == "Assign To" || selectedValue == "") {
                vm.ShowSendResponsediv = true;
                vm.SelectAssignTo = true;
                vm.SelectCloseTicketTo = true;
                vm.IsShowdisableField = true;
            }
        },
        Close() {
            var vm = this;
            this.$emit("close");
        },
        GetEditResultCondition() {
            var vm = this;
            var ResultConditionData = vm.ruleResultEditDataArray;
            if (ResultConditionData[0].TargetSelectValue == "offhours" || ResultConditionData[0].TargetSelectValue == 'locatorarea') {
                if (ResultConditionData[0].TargetSelectValue == "offhours") {
                    vm.IsSelectedValue = "offhours";
                } else {
                    vm.IsSelectedValue = "locatorarea";
                }
                vm.IsShowDdlTarget = false;
                vm.IsShowAddBtn = false;
            }
            vm.WarningMessageTypeId = ResultConditionData[0].WarningMessageTypeId;
            vm.selectedMessegeName = ResultConditionData[0].WarningMessageTypeName;
            if (vm.selectedMessegeName == 'Assign To' || vm.selectedMessegeName == "") {
                vm.ShowSendResponsediv = true;
                vm.SelectAssignTo = true;
                vm.SelectCloseTicketTo = true;
                vm.IsShowdisableField = true;
                if (ResultConditionData != null && ResultConditionData != "") {
                    ResultConditionData.forEach((item, index) => {
                        if (index > 0) {
                            vm.Mainleveldiv.push({
                                selectedValue: '',
                                DDlType: '',
                                TargetSelectValue: ''
                            });
                        }
                        vm.Mainleveldiv[index].selectedValue = item.selectedValue;
                        vm.Mainleveldiv[index].DDlType = item.DDlType;
                        vm.Mainleveldiv[index].TargetSelectValue = item.TargetSelectValue;
                    });
                }
            }
            else if (vm.selectedMessegeName == 'Close Ticket') {
                vm.ShowSendResponsediv = true;
                vm.SelectAssignTo = false;
                vm.SelectCloseTicketTo = false;
                vm.IsShowdisableField = false;
                if (ResultConditionData != null && ResultConditionData != "") {
                    ResultConditionData.forEach((item, index) => {
                        var testDataMultiselect = '';
                        var MultiselectFieldValue = [];
                        if (item.selectedValue.length > 1) {
                            var MultiSelect = item.selectedValue.split(',');
                        } else {
                            var MultiSelect = item.selectedValue;
                        }
                        if (MultiSelect.length > 1) {
                            item.DDlType.forEach((itm, index) => {
                                MultiSelect.forEach((items, index) => {
                                    if (items == itm.value) {
                                        MultiselectFieldValue.push(itm);
                                    }
                                })
                            })
                            testDataMultiselect = MultiselectFieldValue;
                        } else {
                            var filterRuleName = {};
                            filterRuleName = item.DDlType.filter(x => x.value == MultiSelect);
                            var testDataMultiselect = filterRuleName;
                        }
                        vm.SelectedUser = testDataMultiselect;
                        vm.EmailTemplate = item.ResultChainGroup;
                        vm.Mainleveldiv = [{ selectedValue: vm.SelectedUser, DDlType: vm.GetCompanyUserByCompanyId, TargetSelectValue: 'User' }];
                    });
                }
            }
            else if (vm.selectedMessegeName == 'Send Email Notification') {
                vm.ShowSendResponsediv = true;
                vm.SelectAssignTo = false;
                vm.SelectCloseTicketTo = true;
                vm.IsShowdisableField = false;
                if (ResultConditionData != null && ResultConditionData != "") {
                    ResultConditionData.forEach((item, index) => {
                        var testDataMultiselect = '';
                        var MultiselectFieldValue = [];
                        if (item.selectedValue.length > 1) {
                            var MultiSelect = item.selectedValue.split(',');
                        } else {
                            var MultiSelect = item.selectedValue;
                        }
                        if (MultiSelect.length > 1) {
                            item.DDlType.forEach((itm, index) => {
                                MultiSelect.forEach((items, index) => {
                                    if (items == itm.value) {
                                        MultiselectFieldValue.push(itm);
                                    }
                                })
                            })
                            testDataMultiselect = MultiselectFieldValue;
                        } else {
                            var filterRuleName = {};
                            filterRuleName = item.DDlType.filter(x => x.value == MultiSelect);
                            var testDataMultiselect = filterRuleName;
                        }
                        vm.SelectedUser = testDataMultiselect;
                        vm.EmailTemplate = item.ResultChainGroup;
                        vm.NotificationList = item.ResultOtherId;
                        vm.Mainleveldiv = [{ selectedValue: vm.SelectedUser, DDlType: vm.GetCompanyUserByCompanyId, TargetSelectValue: 'User' }];
                    });
                }
            }
            else if (vm.selectedMessegeName == 'Send Response To Call center') {
                vm.ShowSendResponsediv = false;
            }
        },
        btnSaveTargetResult() {
            var vm = this;
            var ruleTargetResultArray = [];
            var ruleResultEditDataArray = [];
            var msg = "";
            if (vm.WarningMessageTypeId == '') {
                vm.ShowAlert(vm.$t('WarningMessegeTypeReq'), 'warning', true, vm.$t('Alert'));
            }
            vm.$refs.ManageRuleEngineResultTarget.validate().then(result => {
                if (result || vm.IsSelectedValue == "offhours" || vm.IsSelectedValue == "locatorarea" || vm.selectedMessegeName == "Send Response To Call center") {
                    vm.Mainleveldiv.forEach((item, index) => {
                        var filterRuleName = {};
                        if (vm.IsSelectedValue != "offhours" && vm.IsSelectedValue != "locatorarea") {
                            if (item.TargetSelectValue == 'User') {
                                selectedValueName = 'User';
                            } else {
                                filterRuleName = item.DDlType.filter(x => x.value == item.selectedValue);
                                var selectedValueName = filterRuleName[0].text;
                            }
                        }
                        if (index > 0) {
                            msg += vm.$t("And") + " ";
                        }
                        if (item.TargetSelectValue == "department") {
                            msg += item.TargetSelectValue + " Equals to " + selectedValueName + " ";
                        } else if (item.TargetSelectValue == "skill") {
                            msg += item.TargetSelectValue + " Equals to " + selectedValueName + " ";
                        } else if (item.TargetSelectValue == "experience") {
                            msg += item.TargetSelectValue + " Equals to " + selectedValueName + " ";
                        } else if (item.TargetSelectValue == "serviceboard") {
                            msg += item.TargetSelectValue + " Equals to " + selectedValueName + " ";
                        } else if (item.TargetSelectValue == "User") {
                            if (vm.selectedMessegeName == "Send Email Notification" || vm.selectedMessegeName == "Close Ticket") {
                                var dataName = '';
                                vm.SelectedUser.forEach((itm, index) => {
                                    if (index == 0) {
                                        dataName = itm.text;
                                    }
                                    else {
                                        dataName += ',' + itm.text;
                                    }
                                });
                                msg += item.TargetSelectValue + " Equals to " + dataName + " ";
                            } else {
                                filterRuleName = item.DDlType.filter(x => x.value == item.selectedValue);
                                var selectedValueName = filterRuleName[0].text;
                                msg += item.TargetSelectValue + " Equals to " + selectedValueName + " ";
                            }
                        } else if (item.TargetSelectValue == "offhours") {
                            msg += 'Based On Off Hours';
                        } else if (item.TargetSelectValue == "locatorarea") {
                            msg += 'Locator Area';
                        }
                        var ids = null;
                        var otherIds = null;
                        var ResultChainIds = '';
                        if (vm.selectedMessegeName != "Send Email Notification" && vm.selectedMessegeName != "Close Ticket") {
                            ResultChainIds = item.selectedValue;
                        }
                        if (selectedValueName == "serviceboard") {
                            ids = vm.EmailTemplate;
                        }
                        else if (vm.selectedMessegeName == "Send Email Notification" || vm.selectedMessegeName == "Close Ticket") {
                            ids = vm.EmailTemplate;
                            otherIds = vm.NotificationList;
                            var dataValue = '';
                            vm.SelectedUser.forEach((itm, index) => {
                                if (index == 0) {
                                    dataValue = itm.value;
                                }
                                else {
                                    dataValue += ',' + itm.value;
                                }
                            })
                            ResultChainIds = dataValue;
                        }
                        var RuleTargetResultObj = {
                            WarningMessageTypeId: vm.WarningMessageTypeId,
                            ResultChainIds: ResultChainIds,
                            ResultType: item.TargetSelectValue,
                            ResultChainGroup: ids,
                            ResultOtherId: otherIds,
                        };
                        ruleTargetResultArray.push(RuleTargetResultObj);

                        var RuleTargetResultObjForEdit = {
                            WarningMessageTypeName: vm.selectedMessegeName,
                            WarningMessageTypeId: vm.WarningMessageTypeId,
                            selectedValue: ResultChainIds,
                            DDlType: item.DDlType,
                            TargetSelectValue: item.TargetSelectValue,
                            ResultChainGroup: ids,
                            ResultOtherId: otherIds,
                        };
                        ruleResultEditDataArray.push(RuleTargetResultObjForEdit);
                    });
                    var index = vm.ResultIndex == undefined ? 0 : vm.ResultIndex;
                    vm.$emit('GetformResultFields', ruleTargetResultArray, msg, ruleResultEditDataArray, index);
                    vm.Close();
                }
            })
        }
    }
}
</script>

